.mission-page {
  margin: 15px;
  padding: 50px 50px 50px 50px;
  background-color: white; /* Imposta lo sfondo bianco per la sezione home-page */
}


.section-mission{
  
  padding: 95px 0 120px;
  opacity: 1;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
}


.container-mission{
  margin: 0 auto;
  max-width: 1400px;
}

.title-row-mission{
  
  font-size: 25px;
  opacity: 1;
}



.para-row-mission{
  box-shadow: 0 2px 8px #0b0e1129;
  border-radius: 8px;
  padding:30px 50px;
}

.font-span-mission {
  color: #f0b90b;
  font-weight: 600;
}

.features-section-mission{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px; /* Sottrai il margine esterno per allineare ai bordi esterni */
  margin-bottom: 60px;
}

.feature-mission{
  width: calc(25% - 20px); /* Sottrai il doppio del margine per mantenere la distanza */
  margin: 10px;
  border: 2px solid #333;
  border-radius: 25px;
  padding: 20px;
  box-sizing: border-box; /* Include padding e bordo nella larghezza */

}

@media (max-width: 600px) {
  .feature-mission {
    width: calc(50% - 20px); /* Aumenta la larghezza per viewport più piccole */
  }
}
  .feature-mission {
    margin: 10px; /* Aumenta o regola il margine se necessario */
  }


