/* Importa un font da Google Fonts */
@import url('https: //fonts.googleapis.com/css2?family=IBM+Plex+Sans+Devanagari:wght@100;200;300;400;500;600;700&display=swap" rel="stylesheet');

body, html {
  margin: 0;
  padding: 0;
  font-family: IBM Plex Sans Devanagari,sans-serif!important;
  
  
}

h1 {
  font-size: 2.5em; /* Aumenta la dimensione del titolo */
  margin: 0.67em 0; /* Aggiunge spazio sopra e sotto il titolo */
}

h2 {
  font-size: 2em;
  margin: 0.75em 0;
  font-weight: 700; /* Rende il titolo grassetto */
}

p {
  font-size: 1em;
  line-height: 1.5; /* Aumenta l'interlinea */
  margin-bottom: 1em; /* Aggiunge spazio sotto i paragrafi */
}

.home-page {
  margin: 15px;
  padding: 50px 50px 50px 50px;
  background-color: white; /* Imposta lo sfondo bianco per la sezione home-page */
}

/* Stili per l'header comune a tutte le pagine */
.home-header {
  display: flex;
  flex-direction: row; /* Garantisce che il contenuto sia disposto in fila */
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 15px;
  border-radius: 15px;
  background-image: url('./icona-1.webp'); /* Modificato per usare un percorso relativo */
  background-size: cover;
  background-position: center;
}

.header-content {
  width: 40%;
  min-height: 200px; /* Modificato per garantire una minima altezza */
  box-shadow: 0 2px 8px #0b0e1129;
  background-color: #f0f0f0;
  padding: 20px; /* Aggiunto padding per evitare che il testo tocchi i bordi */
  margin: 0 20px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-logo {
  opacity: 0.5;
  max-width: 100%; /* Modificato per garantire che l'immagine non superi la larghezza del contenitore */
  height: auto; /* Aggiunto per mantenere le proporzioni dell'immagine */
  border-radius: 20px;
}

@media (max-width: 768px) {
  .home-header {
    flex-direction: column; /* Imposta l'header in colonna su dispositivi mobili */
    padding: 10px; /* Riduce il padding */
  }

  .header-content, .header-logo {
    width: 100%; /* Permette al contenuto e al logo di espandersi */
    margin: 10px 0; /* Aggiusta i margini per dispositivi mobili */
  }
}

/* Stili per la barra di navigazione */
nav ul {
list-style-type: none;
margin: 0;
padding: 0;
background-color: #333;
overflow: hidden;
}

nav li {
float: left;
}

nav li a {
display: block;
color: white;
text-align: center;
padding: 14px 16px;
text-decoration: none;
}

nav li a:hover {
background-color: #ddd;
color: black;
}

.menu-fixed {
  position: fixed; /* Oppure 'sticky' se preferisci che diventi fisso solo scorrendo */
  top: 0; /* Allinea il menu in alto alla viewport */
  left: 0; /* Allinea il menu a sinistra (per coprire l'intera larghezza se necessario) */
  width: 100%; /* Assicura che il menu si estenda per tutta la larghezza della viewport */
  z-index: 1000; /* Assicura che il menu sia sopra gli altri elementi della pagina */
  background: #fff; /* Metti qui il colore di sfondo del menu */
  box-shadow: 0 2px 5px rgba(0,0,0,0.2); /* Opzionale: aggiunge una leggera ombra sotto il menu per migliorare il contrasto */
}


.hamburger-menu {
  display: none; /* Nascosto di default, mostrato solo sotto una certa larghezza */
  cursor: pointer;
  flex-direction: column;
  justify-content: space-around;
  width: 40px; /* Aumentata la larghezza */
  height: 30px; /* Aumentata l'altezza */
  position: absolute;
  top: 20px; /* Distanza dal bordo superiore */
  right: 20px; /* Distanza dal bordo destro */
  z-index: 1000; /* Assicurati che sia sopra agli altri elementi */
}

@media (max-width: 768px) {
  .hamburger-menu {
    display: flex; /* Mostra il menu a hamburger sui dispositivi mobili */
  }

  .hamburger-menu div {
    height: 4px; /* Aumentata l'altezza delle linee */
    width: 100%; /* Assicurati che le linee occupino tutta la larghezza del menu */
    background-color: #333; /* Colore delle linee */
    border-radius: 10px; /* Rende le linee leggermente arrotondate agli angoli */
    transition: all 0.3s linear; /* Rende le transizioni di trasformazione più fluide */
  }

  .nav-links {
    display: none; /* Nascosti di default, mostrati quando il menu è aperto */
    position: absolute;
    right: 0; /* Posizionati a partire dal bordo destro del div .menu-fixed */
    top: 60px; /* Distanziati dal menu a hamburger */
    background-color: #fff; /* Sfondo dei link, scegli un colore adatto */
    border-radius: 0 0 10px 10px; /* Bordi arrotondati per il menu dropdown */
    box-shadow: 0 8px 16px rgba(0,0,0,0.1); /* Ombra per dare profondità */
    width: auto; /* Adatta la larghezza al contenuto o impostala esplicitamente */
  }

  .nav-links.open {
    display: block; /* Mostra i link quando il menu è aperto */
  }

  /* Stili aggiuntivi per i link all'interno del menu a hamburger aperto */
  .nav-links li {
    text-align: right; /* Allinea i testi dei link a destra */
    padding: 10px 20px; /* Spaziatura interna per ogni link */
  }

  .nav-links li a {
    color: #333; /* Colore del testo dei link */
    text-decoration: none; /* Rimuove il sottolineato dai link */
    display: block; /* I link occupano l'intera larghezza del loro contenitore li */
  }
}

/* Stili per il footer */
footer {
background-color: #131212;
color: white;
text-align: center;
padding: 10px 0;
position: fixed;
bottom: 0;
width: 100%;
}

.features-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; /* Cambiato da 'space-between' a 'space-around' per migliorare l'allineamento */
  margin: 0 -10px;
  margin-bottom: 60px;
}

.feature {
  flex-basis: calc(33.333% - 20px); /* Usa 'flex-basis' invece di 'width' per una gestione flex più accurata */
  margin: 10px;
  border: 2px solid #333;
  border-radius: 25px;
  padding: 20px;
  box-sizing: border-box;
}

/* Aggiustamento per schermi grandi dove non si vuole che le feature vadano a capo */
@media (min-width: 1024px) {
  .feature {
    flex-basis: calc(33.333% - 20px); /* Assicura che tre feature riempiano la riga */
  }
}

/* Regolazioni per schermi medi (tablet, ecc.) */
@media (max-width: 1023px) and (min-width: 601px) {
  .feature {
    flex-basis: calc(50% - 20px); /* Due feature per riga */
  }
}

/* Regolazioni per schermi piccoli (smartphone, ecc.) */
@media (max-width: 600px) {
  .feature {
    flex-basis: 100%; /* Una feature per riga */
  }
}

.feature img {
  width: 100%;
  max-width: 200px; /* Adatta questa dimensione in base al layout desiderato */
  border-radius: 10px;
  margin-bottom: 10px;
}

.feature h2, .feature p {
  margin: 10px 0; /* Assicura un margine uniforme per titoli e paragrafi */
}

